/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql } from "gatsby"

import Header from "./header"
import "./layout.scss"

const Layout = ({ children }) => (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
            subtitle
          }
        }
      }
    `}
    render={data => (
      <div className="container-fluid site-container d-flex">
        <div className="row flex-fill">
          <div className="d-none d-sm-block col-sm-4 position-fixed">
            <Header siteTitle={data.site.siteMetadata.title} subTitle={data.site.siteMetadata.subtitle} />
          </div>
          <div className="d-sm-none d-block col-12">
            <Header siteTitle={data.site.siteMetadata.title} subTitle={data.site.siteMetadata.subtitle} />
          </div>
          <div className="col-12 col-sm-8 offset-sm-4 d-flex pr-0">
            <div className="d-flex flex-column justify-content-between w-100">
              <main className="flex-fill w-100 d-flex">{children}</main>
              <footer>
                © {new Date().getFullYear()}, Built with
                {` `}
                <a href="https://www.gatsbyjs.org">Gatsby</a>
              </footer>

            </div>
          </div>
        </div>

      </div>      
    )}
  />
)

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
