import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

const links = [
  {
    'title': 'Home',
    'uri': '/',
  },
  {
    'title': 'About Me',
    'uri': '/about',
  },
  {
    'title': 'Skills',
    'uri': '/skills',
  },
  {
    'title': 'Blog',
    'uri': '/blog',
  },
]

const Header = ({ siteTitle, subTitle }) => (
  <header className="container-fluid">
    <section className="row">

      <div className="col-12 pb-2">
        <h1>
          <Link to="/">
            {siteTitle}
          </Link>
        </h1>
        <h5>{subTitle}</h5>
      </div>
      <nav className="col-12 d-flex flex-column">
        {
          links.map((link, idx) => (
            <div className="py-2" key={idx}>
              <Link to={link.uri}>
                {link.title}
              </Link>
            </div>
          ))
        }
        <div className="py-2">
          <a href="https://github.com/THISS" target="__blank">GitHub</a>
        </div>
        <div className="py-2">
          <a href="https://www.linkedin.com/in/brenton-holswich/" target="__blank">linkedIn</a>
        </div>
      </nav>
    </section>

  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
  subTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
  subTitle: ``,
}

export default Header
